<template>
  <!-- begin:: Transformation Table-->
  <div class="tab-content">
    <WidgetResultTable
      trans_type="domain"
      :dataset_list="this.dataset_list"
      :metric_list="this.metric_list"
      :available_trans_list="this.available_trans_list"
    ></WidgetResultTable>

    <WidgetResultTable
      trans_type="ut"
      :dataset_list="this.dataset_list"
      :metric_list="this.metric_list"
      :available_trans_list="this.available_trans_list"
    ></WidgetResultTable>

    <WidgetResultTable
      trans_type="domain_domain"
      v-if="this.show_domain_domain"
      :dataset_list="this.dataset_list"
      :metric_list="this.metric_list"
      :available_trans_list="this.available_trans_list"
    ></WidgetResultTable>

    <WidgetResultTable
      trans_type="domain_ut"
      v-if="this.show_ut"
      :dataset_list="this.dataset_list"
      :metric_list="this.metric_list"
      :available_trans_list="this.available_trans_list"
    ></WidgetResultTable>

    <WidgetResultTable
      trans_type="ut_ut"
      v-if="this.show_ut_ut"
      :dataset_list="this.dataset_list"
      :metric_list="this.metric_list"
      :available_trans_list="this.available_trans_list"
    ></WidgetResultTable>

    <WidgetResultTable
      trans_type="subpopulation"
      v-if="this.show_ut_ut"
      :dataset_list="this.dataset_list"
      :metric_list="this.metric_list"
      :available_trans_list="this.available_trans_list"
    ></WidgetResultTable>
  </div>

  <!-- end:: UT Transformation Table-->
</template>

<script>
import Vue from "vue";
import "xe-utils";
import VXETable from "vxe-table";
import "vxe-table/lib/style.css";
// const WidgetResultTable = () =>
//   import("@/view/content/widgets/list/widget-result-table.vue");
import WidgetResultTable from "@/view/content/widgets/list/widget-result-table.vue";
Vue.use(VXETable);
export default {
  props: ["dataset_list", "metric_list", "available_trans_list"],
  data() {
    return { show_ut: true, show_ut_ut: false, show_domain_domain: false };
  },
  mounted() {
    // this.show_domain_domain = true
    // this.show_ut_ut = true
    setTimeout(() => {
      // Remove page loader after some time
      this.show_domain_domain = true;
    }, 1000);
    setTimeout(() => {
      // Remove page loader after some time
      this.show_ut = true;
    }, 500);
    setTimeout(() => {
      // Remove page loader after some time
      this.show_ut_ut = true;
    }, 1500);
  },
  components: {
    WidgetResultTable,
  },
};
</script>
